@use 'base/variables' as v;
@use 'base/mixins' as m;


@media (prefers-color-scheme: dark){
    $grisOscuro: #2c2c3b;
    body{
        background-color: $grisOscuro;
    }

    .tagline,
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .passforgot .acciones a,
    .acciones a{
        color:v.$blanco;
    }
    .descripcion-pagina,
    .formulario .campo input,
    .nombre-pagina{
        color: darken(v.$grisClaro,15%);
    }

    .formulario .campo input{
        background-color: lighten($color: $grisOscuro, $amount: 15%);
    }

    .alerta.error{
        background-color: v.$blanco;
    }

    .barra-mobile,.barra{
        background-color: darken($color: $grisOscuro, $amount: 8%);
    }

    .barra{
        p{
            color: v.$grisClaro;

            span{
                color: v.$naranja;
            }
        }
    }

    .filtros{
        background-color: lighten($color: $grisOscuro, $amount: 10%);

        h2,label{
            color: v.$blanco;
        }
        input[type="radio"]{
            accent-color: v.$naranja;
        }
    }

    .dashboard{
        background-color: $grisOscuro;

        .enlace{
            color: v.$grisClaro;

            &:hover{
                background-color: darken($color: v.$grisClaro, $amount: 20%);
            }
        }
    }



    .listado-proyectos .proyecto{
        background-color: lighten($color: $grisOscuro, $amount: 20%);

        &:hover{
            background-color: lighten($color: $grisOscuro, $amount: 8%);

        }
    }

    .listado-tareas {
        .tarea{
            background-color: lighten($color: $grisOscuro, $amount: 10%);
            border: none;
    
            p{
                color: v.$grisClaro;
            }
        }

        .no-tareas{
            color: v.$grisClaro;
        }
    }


    .formulario.nueva-tarea{
        background-color: lighten($color: $grisOscuro, $amount: 10%);
        
        legend{
            color: v.$grisClaro;
        }
        input{
            color: v.$grisOscuro;
        }
    }
}