@use 'base/variables' as v;
@use 'base/mixins' as m;

.ventana {
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    

    .formulario,.formulario-proyecto {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        border-radius: .7rem;
        transform: translateY(-30rem);
        opacity: 0;
        transition: all 500ms ease-in;

        input[type="text"]{
            border: 1px v.$gris solid;
            border-radius: .5rem;
        }
        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }

        legend{
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }

        .opciones{
            display: flex;
            justify-content: space-between;
        }
        .submit-nueva-tarea,.editar-proyecto{
            @include m.boton(v.$indigo,v.$blanco);
            flex: 0;
        }

        .cerrar-ventana{
            @include m.boton(v.$naranja,v.$blanco);
            flex: 0;
        }
    }
}