@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar {
    background-color: v.$indigo;
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: opacity 100ms ease-in;

    &.mostrar {
        opacity: 1;
        height: 100%;
        padding: 2rem;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgb(0 0 0 /.90);

        @include m.tablet {
            display: none;
        }
    }

    &.ocultar{
        opacity: 0;
    }

    @include m.tablet {
        opacity: 1;
        height: 100%;
        padding: 2rem;
        background-color: v.$indigo;
    }

    .contenedor-sidebar {
        display: flex;
        justify-content: space-between;
        align-items: center;

        h2 {
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }

        .cerrar-menu {
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;
            img{
                &:hover{
                    cursor: pointer;
                }
            }

            @include m.tablet{
                display: none;
            }
        }
    }

    .sidebar-nav {
        margin-top: 5rem;

        a {
            color: v.$blanco;
            font-size: 2.3rem;
            display: block;
            margin-bottom: 2rem;
            padding: .7rem;
            text-align: center;
            transition: background-color 300ms ease;

            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }

            @include m.tablet{
                font-size: 1.3rem;
                margin-bottom: 1rem;
                text-align: left;
            }
        }
    }

    .cerrar-sesion-mobile{
        margin-top: 10rem;
        
        .cerrar-sesion{
            @include m.boton(v.$indigo,v.$blanco)
        }

        @include m.tablet{
            display: none;
        }
    }
}